import Favorable from "@/script/enum/favorable";
import Period from "@/script/enum/period";
import Ruler from "@/script/enum/ruler";

function ToNight(i) {
    //Return night ruler of a day ruler
    switch (parseInt(i)) {
        case Ruler.Saturn: return Ruler.Mercury
        case Ruler.Jupiter: return Ruler.Moon
        case Ruler.Mars: return Ruler.Saturn
        case Ruler.Sun: return Ruler.Jupiter
        case Ruler.Venus: return Ruler.Mars
        case Ruler.Mercury: return Ruler.Sun
        case Ruler.Moon: return Ruler.Venus
    }
}
function FromDateTime(i) {
    //return Day (monday:1,sunday:7) ruler
    switch (parseInt(i)) {
        case 1: return Ruler.Moon;
        case 2: return Ruler.Mars;
        case 3: return Ruler.Mercury;
        case 4: return Ruler.Jupiter;
        case 5: return Ruler.Venus;
        case 6: return Ruler.Saturn;
        default: return Ruler.Sun;
    }
}
function FromInt(i) {
    //int to enum conversion
    const values = [null, Ruler.Saturn, Ruler.Jupiter, Ruler.Mars, Ruler.Sun, Ruler.Venus, Ruler.Mercury, Ruler.Moon];
    return values[i];
}
function GetFromDayHour(jour, hour) {
    //Calculate hourly ruler for day
    const start = FromInt(jour)

    let value = start + hour;
    return FromInt(value % 7 !== 0 ? value % 7 : 7)
}
function getRelationperiod (day, hour) {
    return getDayRelation(day,hour)
    return period===Period.Day ? getDayRelation(day,hour):getNightRelation(day,hour)
}
function getDayRelation(day, hour) {
    /*
            - Samedi, jour de Saturne, il vaut mieux ne pas utiliser Jupiter ou Mercure.
            - Dimanche, jour du Soleil, ne pas utiliser Mars.
            - Lundi, jour de la Lune, pas de plante hostile.
            - Mardi, jour de Mars, ne pas utiliser le Soleil.
            - Mercredi, jour de Mercure, ne pas utiliser Saturne.
            - Jeudi, jour de Jupiter, ne pas utiliser Saturne.
            - Vendredi, jour de Vnus, pas de plante hostile.
                Par contre, il y a des combinaisons favorables :
                - Lundi, Mercure est en sympathie.
            - Mercredi, Lune ou Soleil sont en sympathie.
            - Vendredi, le Soleil est en sympathie.
                */
    if (day === Ruler.Saturn && (hour === Ruler.Jupiter || hour === Ruler.Mercury)) return Favorable.Unfavorable;
    if (day === Ruler.Sun && hour === Ruler.Mars) return Favorable.Unfavorable;
    if (day === Ruler.Mars && hour === Ruler.Sun) return Favorable.Unfavorable;
    if (day === Ruler.Mercury && hour === Ruler.Saturn) return Favorable.Unfavorable;
    if (day === Ruler.Jupiter && hour === Ruler.Saturn) return Favorable.Unfavorable;

    if (day === Ruler.Sun && hour === Ruler.Mercury) return Favorable.Favorable;
    if (day === Ruler.Mercury && (hour === Ruler.Mercury || hour === Ruler.Moon)) return Favorable.Favorable;
    if (day === Ruler.Venus && hour === Ruler.Sun) return Favorable.Favorable;

    if (day === hour) return Favorable.Favorable;

    return Favorable.Neutral;
}
function getNightRelation(day, hour) {
    let dayRelation = getDayRelation(day,hour)
    let night = ToNight(day)

    //Nuit de mardi  mercredi, commence par Saturne, au-dessus de Mars dans l'Arbre de Vie.
    // Les aspects ngatifs de nuit doivent ainsi tre affaiblis
    //le defavorable devient neutre.
    if(day===Ruler.Mars) return dayRelation === Favorable.Unfavorable ? Favorable.Neutral :Favorable.Favorable
    // Pas sur de la force du Geni Diurne sur le GeniNocturne
    if (dayRelation ===  Favorable.Unfavorable ) return Favorable.Unfavorable

    let nightRelation = getDayRelation(night,hour)

    //Si c'est dfavorable au geni nocturne, defavorable
    if (nightRelation ===  Favorable.Unfavorable ) return Favorable.Unfavorable

    //Nuit de vendredi  samedi, prside par Mars, le complment de Vnus. C'est la nuit du
    // Vendredi Saint, celle o les lments combins de Vnus et de Mars prparent  l'Epreuve del'Initiation
    //le neutre devient favorable, je me demande si le defavorable saute pas non plus
    if(day===Ruler.Venus) return Favorable.Favorable

    // Pas sur de la force du Geni Diurne sur le GeniNocturne
    return nightRelation ===  Favorable.Favorable ||dayRelation ===  Favorable.Favorable  ? Favorable.Favorable:Favorable.Neutral;
}

function getRelation(period, day, hour) {
    return getDayRelation(day,hour)
    return period===Period.Day ? getDayRelation(day,hour):getNightRelation(day,hour)
}

export default {
    ToNight,
    FromDateTime,
    FromInt,
    GetFromDayHour,
    getRelation,
    getRelationperiod,
    getDayRelation,
    getNightRelation,
}