import Zodiac from "@/script/enum/zodiac";
import Ruler from "@/script/enum/ruler";
import Element from "@/script/enum/element";

export function zodiacToElement(zodiac) {
    switch (zodiac) {
        case Zodiac.Virgo:
        case Zodiac.Taurus:
        case Zodiac.Capricorn:
            return Element.Terre;
        case Zodiac.Cancer:
        case Zodiac.Scorpio:
        case Zodiac.Pisces:
            return Element.Eau;
        case Zodiac.Gemini:
        case Zodiac.Libra:
        case Zodiac.Aquarius:
            return Element.Air;
        default:
            return Element.Feu;
    }
}

export function rulerToElement(ruler) {
    switch (ruler) {
        case Ruler.Sun:
        case Ruler.Mars:
            return Element.Feu;
        case Ruler.Mercury:
        case Ruler.Saturn:
            return Element.Terre;
        case Ruler.Venus:
        case Ruler.Jupiter:
            return Element.Air;
        case Ruler.Moon:
        default:
            return Element.Eau;
    }
}

export function zodiacToRuler(zodiac) {
    switch (zodiac) {
        case Zodiac.Aries:
        case Zodiac.Scorpio:
            return Ruler.Mars
        case Zodiac.Taurus:
        case Zodiac.Libra:
            return Ruler.Venus
        case Zodiac.Pisces://neptune
        case Zodiac.Sagittarius:
            return Ruler.Jupiter
        case Zodiac.Cancer:
            return Ruler.Moon
        case Zodiac.Leo:
            return Ruler.Sun
        case Zodiac.Virgo:
        case Zodiac.Gemini:
            return Ruler.Mercury
        case Zodiac.Capricorn:
        case Zodiac.Aquarius://uranus en modern
            return Ruler.Saturn;
    }
}
